.students-table table{width:100%;text-align:left}.students-table table .avtr{width:42px;height:42px;display:block;background-size:cover;background-position:center center;border-radius:5px;background-color:#1d2d4a}.students-table table th.asc::after{content:"↑"}.students-table table th.desc::after{content:"↓"}.students-table table th.sortable{cursor:pointer;position:relative}.students-table table th.sortable::after{content:"⇅"}